/* 主要内容区域 */
.post-content {
    max-width: 640px;
    margin: 0 auto;
    // padding: 0 .5rem;
    box-sizing: border-box;
}

/* 确保文本正常换行 */
.post-content, .post-content * {
    word-wrap: break-word;
    overflow-wrap: break-word;
}

/* 子元素间距 */
.post-content > * {
    margin-bottom: 1rem;
}

.pad {
    margin-top: 10px;
    margin-bottom: 70px;
}

/* 图片、表格、代码块等块级元素 */
// .post-content img, .post-content table, .post-content pre {
//     max-width: 100%;
//     height: auto;
//     display: block;
//     margin: 1rem 0;
// }

/* 小屏幕设备优化 */
@media (max-width: 768px) {
    .post-content {
        padding: 0;
    }

    // .post-content img, .post-content table, .post-content pre {
    //     margin: 0.5rem 0;
    // }
}


/* 分隔线与导航容器 */
#post-hr {
  border: none;
  border-top: 1px dashed #CC6D96;
//   margin: 10px 0;
}

.post-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  color: #555;
  margin: 5px 0;
  padding: 5px 0;
  text-align: center;
  gap: 10px; /* 间距更清晰 */
}

.post-nav a {
  color: #CC6D96;
  text-decoration: none;
  padding: 8px 12px;
  border-radius: 6px;
  transition: all 0.3s ease;
  flex: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* 左侧链接：前一篇 */
.post-nav > :first-child {
  text-align: left;
  color: #CC6D96;
}

/* 中间链接：随便逛逛 */
.post-nav > :nth-child(2) {
  text-align: center;
  color: #CC6D96;
  font-weight: 500;
//   background-color: #fef8f0;
//   border: 1px dashed #e67e22;
}

// .post-nav > :nth-child(2):hover {
//   background-color: #e67e22;
//   color: white;
// }

/* 右侧链接：下一篇 */
.post-nav > :last-child {
  text-align: right;
  color: #CC6D96;
}

/* 悬停效果 */
// .post-nav a:hover {
//   background-color: #f5f5f5;
//   transform: translateY(-2px);
// }

/* 响应式：小屏幕堆叠显示 */
@media (max-width: 600px) {
  .post-nav {
    flex-direction: column;
    gap: 10px;
  }

  .post-nav a {
    text-align: center !important;
  }
}