/* 桌面端浮动 TOC 带边框背景图 */
.post-toc {
  position: fixed;
  top: 150px;
  right: 35px;
  width: 12vw;
  max-width: 400px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-top: 2px solid #FEE3FD;

  max-height: calc(100vh - 650px); /* 最大不超过视口，留出上下边距 */
  overflow-y: auto; /* 内容超了才滚动 */

  font-size: 90%;
  padding: 2px;
  // box-sizing: border-box;
  background: rgb(232, 216, 255);
  background: linear-gradient(45deg, rgba(254, 227, 253,0.8) 10%, rgba(207, 207, 205,0.8) 100%);

  /* 可选：最小高度防止太小 */
  min-height: 60px;
}

// .toc-content {
//     background-color: var(--neodb-card-color, #ebe8f1);
// }

/* 默认隐藏移动 TOC */
.post-toc-mobile {
  display: none;
}

#toc-title {
  text-align: center;
  margin-top: 10px;
}

@media only screen and (max-width: 1224px) {
  /* 移动端显示 mobile TOC，隐藏桌面 TOC */
  .post-toc {
    display: none;
  }

  .post-toc-mobile {
    display: block;
    margin-bottom: 2rem;
    font-size: 90%;
  }

  .post-toc-mobile details {
    border-width: 7px;
    border-style: solid;
    border-image: url("/imgs/toc2.png") 7 fill round;

    padding: 0.75rem;
    border-radius: 6px;
    background-color: var(--neodb-card-color, #ffffff);
    box-sizing: border-box;
  }

  .post-toc-mobile summary {
    font-weight: bold;
    cursor: pointer;
    font-size: 90%;
  }
}
