
.note__item {
  // background: rgba(255, 255, 255, 0.8);
  border: 3px dashed #b39eb5;
  border-radius: 15px;
  padding: .1rem .1rem .1rem .4rem;
  // padding: 0.5rem;
  margin-bottom: 0.5rem;
  position: relative; /* 启用相对定位，为子元素绝对定位提供参照 */
  transition: all 0.3s ease;
}

.note__item::before {
  content: "🎀";
  position: absolute; /* 关键：使用绝对定位 */
  top: -12px;         /* 调整位置，使其出现在边框上方或对齐上边框 */
  left: 12px;         /* 贴近左边框，稍微内缩一点更美观 */
  font-size: 20px;
  transform: rotate(-10deg);
  pointer-events: none; /* 避免装饰元素干扰点击事件 */
}

.note__list {
    color: #666;
    // line-height: 1.2;
}

