.list-container {
  display: flex;
  gap: 10px;
  padding: 10px;
  background-color: #fff;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-top: 4px solid #FEE3FD;
}

.sidebar-left {
  flex: 0 0 110px;
  display: flex;
  align-items: flex-start;
}

.card {
  width: 100px;
  padding: 20px;
  // background-color: #fff;
  border-radius: 20px;
  // box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.logo {
  width: 70px;
  height: 70px;
  margin-bottom: 10px;
}

.card p {
  margin: 0 0 10px;
}

.menuother li {
  list-style: none;
  margin: 10px 0;
  border-top: 1px dashed #000;
  border-bottom: 1px dashed #000;
  padding: 5px 0;
}

.menuother a {
  color: #000;
  text-decoration: none;
}

.main-content {
  flex: 1;
  min-width: 0;
  padding: 0 5px;
}

.title-section h1 {
  text-align: center;
}

.list-section ul {
  list-style-type: none;
  padding: 0;
}

.list-section li {
  margin-bottom: 10px;
}

.sidebar-right {
  flex: 0 0 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 0;
}

.button-group {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.btn {
  width: 40px;
  height: 40px;
  background-color: #FEE3FD;
  border: 2px solid #FBFEEA;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #000;
  text-decoration: none;
  /* 竖排文字：从右到左，文字正向 */
  writing-mode: vertical-rl;
  text-orientation: upright;
  /* 移除 transform: rotate(180deg); */
}

.small-text {
  font-size: 12px;
  margin-top: 10px;
  text-align: center;
}

.mobile-menu-button {
  display: none;
}

/* ========== 移动端响应式 ========== */
@media (max-width: 768px) {
  /* ✅ 显示顶部导航条 */
  .mobile-nav {
    display: block;
    width: 100%;
    // background: white;
    // border-bottom: 1px solid #ddd;
    padding: 12px 0;
    // position: sticky;
    top: 0;
    z-index: 1000;
  }

  /* 隐藏左右两侧 */
  .sidebar-left,
  .sidebar-right {
    display: none;
  }

  /* 主容器变为单列 */
  .list-container {
    flex-direction: column;
    align-items: stretch;
    padding: 16px 0;
    gap: 16px;
  }

  /* 主内容区占满宽度 */
  .main-content {
    order: 1;
  }

  /* 标题稍小 */
  .title-section h1 {
    font-size: 1.5rem;
  }

  .list-section li {
    padding: 10px 0;
  }

  .list-section a {
    font-size: 1rem;
  }
}

/* 桌面端：隐藏 mobile-nav */
@media (min-width: 769px) {
  .mobile-nav {
    display: none;
  }
}