/* 基础重置和字体 */
i { color: #FFB755; font-weight: normal; }
b { color: #CC6D96; }

/* 布局容器 */
#layout {
    margin: 40px auto;
    width: 640px;
}

/* 头部 */
#header1 { width: 640px; float: left; margin: 0 auto; }
#header2 {
    height: 135px;
    background: transparent;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    margin: 0;
}

/* 导航栏 */
#navigation1 { width: 640px; float: left; margin: 0 auto; }
#navigation2 {
    height: 20px;
    line-height: 20px;
    font-size: 16px;
    background: #FEE3FD;
    margin: 0px;
    text-align: center;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-top: 2px solid #FBFEEA;
}
#navigation2 a {
    background: transparent;
    font: 12px ;
    text-transform: uppercase;
    font-style: normal;
    line-height: 20px;
    padding: 4px;
    letter-spacing: 3px;
    margin-right: 6px;
    color: #9D6AA6;
}
#navigation2 a:hover {
    color: #999;
}

/* 三栏布局 */
#left1 { width: 180px; float: left; }
#left2 {
    height: 350px;
    min-height: 300px;
    background: #fff;
    border-top: 1px solid #9D6AA6;
    border-right: 1px solid #ddd;
    margin: 0px;
    padding: 4px;
    overflow: auto;
    overflow-x: hidden;
}

#content1 { width: 280px; float: left; }
#content2 {
    height: 350px;
    min-height: 300px;
    overflow: auto;
    overflow-x: hidden;
    background: #FFFFFF;
    border-top: 1px solid #9D6AA6;
    margin: 0px;
    padding: 4px;
}

#right1 { width: 180px; float: left; }
#right2 {
    height: 350px;
    overflow: auto;
    overflow-x: hidden;
    border-top: 1px solid #9D6AA6;
    background: #fff;
    border-left: 1px solid #ddd;
    margin: 0px;
    padding: 4px;
}

/* 页脚 */
#footer1 { width: 640px; clear: both; }
#footer2 {
    height: 20px;
    line-height: 20px;
    text-align: center;
    background: #FEE3FD;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    margin: 0px;
    border-top: 1px solid #bbb;
}

/* 标题样式 */
.title {
    background: transparent;
    text-transform: uppercase;
    font: bold 12px ;
    color: #666;
    line-height: 10px;
    padding: 1px 1px 1px;
    border-bottom: 1px solid #bbb;
    margin-bottom: 4px;
    letter-spacing: 2px;
}
.bigtitle {
    font-size: 30px;
    text-align: center;
    color: #CC6D96;
    letter-spacing: 1px;
}
.blogcontent {
    font-size: 15px;
}

/* 内容块 */
.box {
    background: #fff;
    border-left: 2px solid #E7DCED;
    border-right: 2px solid #F3EDF6;
    border-bottom: 2px solid #F3EDF6;
    border-top: 2px solid #E7DCED;
    padding: 4px;
    margin-bottom: 4px;
    font-size: 15px;
}

/* 菜单项 */
.menu {
    display: block;
    background: #fff;
    margin-bottom: 1px;
    padding-left: 5px;
    text-transform: uppercase;
    letter-spacing: 1px;
    font: 9px ;
    color: #333;
}
.menu:hover {
    background: #f5f5f5;
    color: #666;
}

/* ============================
   📱 移动端适配（屏幕宽度 ≤ 600px）
   ============================ */
@media (max-width: 600px) {
    /* 布局容器 */
    #layout {
        width: 95%;
        margin: 10px auto;
    }

    /* 头部 */
    #header1, #navigation1, #footer1 {
        width: 100%; /* 全宽显示 */
        float: none;
    }

    /* 三栏变单栏堆叠 */
    #left1, #content1, #right1 {
        width: 100% !important;
        float: none;
        margin-bottom: 10px;
    }

    #left2, #content2, #right2 {
        height: auto !important;
        min-height: 100px;
    }

    /* 右栏去掉左侧边框，避免和容器重叠 */
    #right2 {
        border-left: none;
    }

    /* 头部圆角只保留顶部 */
    #header2 {
        border-radius: 10px;
    }
    #navigation2 {
        border-radius: 0;
    }
    #footer2 {
        border-radius: 10px;
    }

    /* 标题稍大一点，方便点击 */
    .title {
        font-size: 13px;
        padding: 4px;
    }
}