/* 导航栏外层容器 - 负责居中 */
#navigationother {
  width: 100%;
  display: flex;
  justify-content: center; /* ✅ 关键：水平居中子元素 */
  padding: 10px 16px;
  box-sizing: border-box;
}

/* 导航栏主体 - 宽度由内容决定 */
#navigationother2 {
  font-size: 16px;
  background: #FEE3FD;
  padding: 8px 16px;
  border-radius: 10px;
  border-top: 2px solid #FBFEEA;
  display: flex;
  gap: 16px;
  align-items: center;
  justify-content: center; /* 内部按钮居中（可选） */
  width: fit-content;       /* 宽度包裹内容 */
  max-width: 100%;
  min-width: 150px;
  /* 不要设置 float、margin: 0 auto 在这里 */
}

#navigationother2 a {
  background: transparent;
  font: 12px system-ui, sans-serif;
  text-transform: uppercase;
  font-style: normal;
  line-height: 20px;
  padding: 4px 8px;
  letter-spacing: 3px;
  color: #9D6AA6;
  text-decoration: none;
  white-space: nowrap;
  flex-shrink: 0; /* 防止被压缩 */
}

#navigationother2 a:hover {
  color: #777;
}

@media (max-width: 640px) {
  #navigationother2 {
    flex-direction: row;
    flex-wrap: nowrap;
    overflow-x: auto;
    overflow-y: hidden;
    width: 100%;                /* 占满宽度 */
    max-width: 200px;
    padding: 8px 12px;
    gap: 16px;
    // display: inline-flex;       /* 关键：让宽度由内容定 */
    justify-content: center;    /* 按钮组居中 */
    align-items: center;
  }

  #navigationother2::-webkit-scrollbar {
    display: none;
  }

  #navigationother2 a {
    padding: 6px 10px;
    font-size: 13px;
    letter-spacing: 2px;
  }
}
