/* styles.css */
.album-container {
    max-width: 640px;
    margin: 0 auto;
    padding: 20px;
}

.album-header {
    background-color: #f4e8d7;
    padding: 20px;
    border-radius: 5px;
    margin-bottom: 20px;
}

.album-header h1 {
    font-size: 24px;
    margin-top: 0;
}

.announcement {
    margin-top: 20px;
}

.announcement ul {
    list-style-type: none;
    padding: 0;
}

.announcement li {
    margin: 5px 0;
}

.image-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr)); /* 调整最小宽度 */
    gap: 10px;
}


/* styles.css */
.image-item {
    text-align: center; /* 确保标题能够居中对齐 */
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 5px;
    overflow: hidden;
    padding: 3px 0;
}

.image-item img {
    width: auto;
    height: 129px;
    display: block;
}

.flower {
    -webkit-mask-image: url(/imgs/cs5sCEo.png); 
    mask-image: url(/imgs/cs5sCEo.png); 
    
    -webkit-mask-size: 100%; 
    mask-size: 100%; 
    
    -webkit-mask-repeat: no-repeat; 
    mask-repeat: no-repeat; 
    
    -webkit-mask-position: center; 
    mask-position: center; 
}

.image-item .date {
    background-color: #fff; /* 白色背景 */
    padding: 3px; /* 内边距 */
    font-size: 10px; /* 字体大小 */
    color: #333;
    text-align: left; /* 左对齐 */
    margin: 0; /* 移除默认的外边距 */
}

.image-item .title {
    margin: 3px 3px 0px; /* 在图片下方留出空间 */
    font-size: 14px;
    line-height: 1;
    color: #333;
    text-align: center; /* 居中对齐 */
    border: none; /* 确保没有边框 */
    text-decoration: none; /* 确保没有下划线 */
}

.pagination {
    text-align: center;
    margin-top: 20px;
}

.pagination ul {
    list-style-type: none;
    padding: 0;
}

.pagination li {
    display: inline-block;
    margin: 0 5px;
}

.pagination a {
    display: inline-block;
    padding: 5px 10px;
    background-color: #f4e8d7;
    color: #333;
    text-decoration: none;
    border-radius: 3px;
}

.image-grid > p:empty,
.image-grid > p:first-child:empty,
.image-grid > p:last-child:empty {
    display: none;
}

@media (max-width: 768px) {
    .image-grid {
        grid-template-columns: repeat(auto-fill, minmax(100px, 1fr)); /* 减少最小宽度 */
        gap: 10px; /* 减少间距 */
    }

    .image-item {
        margin: 0; /* 减少外边距 */
    }

    .image-item .date, .image-item .title {
        font-size: 10px; /* 减少字体大小 */
    }

    .image-grid > p:empty,
    .image-grid > p:first-child:empty,
    .image-grid > p:last-child:empty {
    display: none;
    }
}

@media (min-width: 769px) and (max-width: 1024px) {
    .image-grid {
        grid-template-columns: repeat(auto-fill, minmax(150px, 1fr)); /* 中等屏幕宽度 */
        gap: 10px;
    }

    .image-item .date, .image-item .title {
        font-size: 10px; /* 保持或适当调整字体大小 */
    }

    .image-grid > p:empty,
    .image-grid > p:first-child:empty,
    .image-grid > p:last-child:empty {
    display: none;
    }
}